.app {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  background: #000;
}

.typewriter {
  color: #fff;
  text-shadow: 0 0 0.5em #fff;
  font-size: 2.5em;
  font-family: 'Courier New', Courier, monospace;
  text-align: center;
  line-height: 1.5em;
  letter-spacing: -0.05em;
  text-transform: uppercase;
  text-indent: 0.5em;
  white-space: pre;
}

.text {
  color: #fff;
  text-shadow: 0 0 0.5em #fff;
  font-size: 2.5em;
  font-family: 'Courier New', Courier, monospace;
  text-align: center;
  line-height: 1.5em;
  letter-spacing: -0.05em;
  text-transform: uppercase;
  text-indent: 0.5em;
  white-space: pre;
}